@import '../styles.scss';

.modal.modal-document-properties {
  table.properties {
    border-collapse: collapse;

    th,
    td {
      padding: $marginHalf 0;
      vertical-align: top;
    }

    th {
      color: $gray;
      font-weight: normal;
      text-align: left;
    }

    td {
      padding-left: $marginTwice;
    }
  }
}
