@import '../styles.scss';

.modal-foreground.share {
  width: 600px;
}

.modal.modal-share {
  .share-contents {
    display: flex;

    .url-contents {
      flex: 1;
      font-size: $font-size;
      background: var(--jse-input-background-readonly);
      color: var(--jse-text-color);
      border: var(--jse-input-border);
      padding: $marginHalf $margin;
      border-radius: $border-radius;
    }
  }
}
