@import '../../styles.scss';

.cloud-document-schema-preview {
  .preview {
    margin-top: $margin;
    border: var(--jse-input-border);
    height: 400px;
    border-radius: $border-radius;
    overflow: auto;

    .loading,
    .error,
    .info {
      margin: $margin;
    }

    .info {
      color: $gray;
    }
  }
}
