@import './styles.scss';

.jsoneditor-react-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-width: 0;
  min-height: 0;

  &.read-only {
    --jse-background-color: transparent;
    --jse-main-border: none;
  }

  // override some of the styling of svelte-jsoneditor
  .jse-menu {
    .fa-icon {
      margin-top: 3px;
    }
  }
}
