@import '../styles.scss';

.modal.modal-prompt {
  form {
    display: flex;
    flex-direction: column;
    gap: $margin;

    .options {
      display: flex;
      align-items: center;
      gap: $margin;

      select {
        font-family: inherit;
        font-size: inherit;
        background: var(--jse-input-background);
        color: var(--jse-text-color);
        border: var(--jse-input-border);
      }
    }
  }
}
